"use client"

import { useEffect, useState } from "react"
import Link from "next/link"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { useContract } from "@/context/contract-context"
import { formatDate, formatAddress } from "@/lib/utils"
import { SkeletonCard } from "@/components/ui/skeleton-card"
import { ArrowUpRight } from "lucide-react"
import { fetchAlCampaigns, fetchAllCampaigns } from "@/lib/graphql"

export function ActiveCampaigns({ searchTerm = "", sortOrder = "latest", filterType = "all" }) {
  const { contract, isConnected } = useContract()
  const [campaigns, setCampaigns] = useState([])
  const [filteredCampaigns, setFilteredCampaigns] = useState([])
  const [loading, setLoading] = useState(true)
  const [usingSubgraph, setUsingSubgraph] = useState(true)

  useEffect(() => {
    loadCampaigns()
  }, [contract, isConnected])

  // Filter and sort campaigns when dependencies change
  useEffect(() => {
    let filtered = [...campaigns]

    // Apply search filter
    if (searchTerm.trim() !== "") {
      const query = searchTerm.toLowerCase()
      filtered = filtered.filter(
        (campaign) =>
          campaign.description.toLowerCase().includes(query) || campaign.creator.toLowerCase().includes(query),
      )
    }

    // Apply status filter
    const now = Math.floor(Date.now() / 1000)
    filtered = filtered.filter((campaign) => {
      switch (filterType) {
        case "active":
          return now >= campaign.startTime && now <= campaign.endTime
        case "upcoming":
          return now < campaign.startTime
        case "ended":
          return now > campaign.endTime
        default:
          return true
      }
    })

    // Apply sorting
    filtered.sort((a, b) => {
      return sortOrder === "latest" ? b.startTime - a.startTime : a.startTime - b.startTime
    })

    setFilteredCampaigns(filtered)
  }, [searchTerm, sortOrder, filterType, campaigns])

  const loadCampaigns = async () => {
    try {
      setLoading(true)

      // First try to load from subgraph
      const subgraphData = await fetchAllCampaigns()

      if (subgraphData && subgraphData.length > 0) {
        setCampaigns(subgraphData)
        setUsingSubgraph(true)
      }
      // Fallback to contract if subgraph fails or is empty and wallet is connected
      else if (contract && isConnected) {
        await fetchCampaignsFromContract()
        setUsingSubgraph(false)
      }
    } catch (error) {
      console.error("Error loading campaigns:", error)

      // Try contract as fallback if wallet is connected
      if (contract && isConnected) {
        await fetchCampaignsFromContract()
        setUsingSubgraph(false)
      }
    } finally {
      setLoading(false)
    }
  }

  const fetchCampaignsFromContract = async () => {
    try {
      const count = await contract.campaignCount()
      const campaignsList = []

      for (let i = 1; i <= count; i++) {
        try {
          const [votingType, restriction, resultType, creator, description] = await contract.getCampaignBasicInfo(i)
          const [status, startTime, endTime] = await contract.getCampaignTimeInfo(i)

          const campaignData = {
            id: i,
            description,
            creator,
            startTime: Number(startTime),
            endTime: Number(endTime),
            status: Number(status),
            restriction: Number(restriction),
            resultType: Number(resultType),
            votingType: Number(votingType),
          }

          campaignsList.push(campaignData)
        } catch (err) {
          console.error(`Error fetching campaign ${i}:`, err)
        }
      }

      setCampaigns(campaignsList)
    } catch (error) {
      console.error("Error fetching campaigns from contract:", error)
    }
  }

  const getStatusBadge = (startTime, endTime) => {
    const now = Math.floor(Date.now() / 1000)

    if (now < startTime) {
      return (
        <Badge variant="outline" className="bg-blue-50 text-blue-600 border-blue-200">
          Upcoming
        </Badge>
      )
    } else if (now < endTime) {
      return <Badge className="bg-green-600">Active</Badge>
    } else {
      return <Badge variant="secondary">Ended</Badge>
    }
  }

  if (loading) {
    return (
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {[1, 2, 3].map((i) => (
          <SkeletonCard key={i} />
        ))}
      </div>
    )
  }

  if (filteredCampaigns.length === 0) {
    return (
      <div className="text-center py-12 bg-background/50 backdrop-blur-sm rounded-lg border">
        <h3 className="text-xl font-medium mb-4">
          {searchTerm ? "No campaigns found matching your search" : "No campaigns found"}
        </h3>
        <p className="text-muted-foreground mb-6">
          {searchTerm ? "Try adjusting your search terms" : "Be the first to create a voting campaign!"}
        </p>
        <Button asChild>
          <Link href="/create">Create Campaign</Link>
        </Button>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {!isConnected && usingSubgraph && (
        <div className="p-4 bg-blue-50 text-blue-700 rounded-lg mb-4">
          <p className="text-sm">
            Viewing data from the Polytix subgraph. Connect your wallet to interact with campaigns.
          </p>
        </div>
      )}

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredCampaigns.map((campaign) => (
          <Card
            key={campaign.id}
            className="group relative overflow-hidden border-border/50 bg-background/50 backdrop-blur-sm hover:shadow-md transition-all duration-300"
          >
            <CardHeader>
              <div className="flex justify-between items-start">
                <CardTitle className="line-clamp-1">{campaign.description}</CardTitle>
                {getStatusBadge(campaign.startTime, campaign.endTime)}
              </div>
              <CardDescription>Created by: {formatAddress(campaign.creator)}</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="text-sm text-muted-foreground space-y-1">
                <div>Start: {formatDate(campaign.startTime * 1000)}</div>
                <div>End: {formatDate(campaign.endTime * 1000)}</div>
                <div className="mt-4 flex flex-wrap gap-2">
                  <Badge variant="outline">{campaign.votingType === 0 ? "Candidate-based" : "Proposal-based"}</Badge>
                  <Badge variant="outline">
                    {campaign.restriction === 0 ? "Open" : campaign.restriction === 1 ? "Limited" : "Registration"}
                  </Badge>
                </div>
              </div>
            </CardContent>
            <CardFooter>
              <Button asChild className="w-full group-hover:translate-y-0 transition-transform">
                <Link href={`/campaigns/${campaign.id}`} className="flex items-center justify-center">
                  View Campaign
                  <ArrowUpRight className="ml-2 h-4 w-4 transition-transform group-hover:translate-x-0.5 group-hover:-translate-y-0.5" />
                </Link>
              </Button>
            </CardFooter>
          </Card>
        ))}
      </div>
    </div>
  )
}
